<template>
    <div class="my-container">
        <el-button type="primary" @click="openFile"></el-button>
        <el-button type="success" @click="downLoadTemplate"></el-button>
        <input 
        ref="pathClear"
        type="file" 
        style="display:none" 
        id="file" 
        @change="upload"
        accept="application/vnd.ms-excel, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet">
        <div class="mt20 flex-row align-items">
          <h1 style="font-size:20px;"></h1>
          <el-select v-model="questionType" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
        </div>
        <!--  -->
        <Single 
        :singleQuestion='singleQuestion'
        @addSingle='addSingle' 
        @deleteItem='deleteItem'></Single>
        <!--  -->
        <Multiple 
        :multipleQuestion='multipleQuestion'
        @addMultiple='addMultiple' 
        @deleteItem='deleteItem'></Multiple>
        <!--  -->
        <Judgement 
        :judgementQuestion='judgementQuestion'
        @addJudgement='addJudgement' 
        @deleteItem='deleteItem'></Judgement>
        <!--  -->
        <Answer 
        :answerQuestion='answerQuestion'
        @addAnswer='addAnswer' 
        @deleteItem='deleteItem'></Answer>
        <div class="mt20">
        <h1 style="font-size:20px;margin-bottom:20px;"></h1>
        <el-checkbox-group v-model="checkList">
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
        <el-checkbox label=""></el-checkbox>
      </el-checkbox-group>
      </div>
        <div class="mt20" v-if="!isShow">
            <el-button type="primary" @click="create"></el-button>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import { post,get } from "../util/http.js";
import {getRandomStr} from '../util/index.js'
export default {
  data() {
    return {
      // 
      singleQuestion: [],
      // 
      multipleQuestion: [],
      // 
      judgementQuestion: [],
      // 
      answerQuestion: [],
      checkList:[],
      questionType:''
    };
  },
  methods: {
    addSingle() {
      let id = getRandomStr();
      this.singleQuestion.push({
        id,
        title: "",
        options: { A: "", B: "", C: "", D: "" },
        answer: "",
        message: ""
      });
    },
    addMultiple() {
      let id = getRandomStr();
      this.multipleQuestion.push({
        id,
        title: "",
        options: { A: "", B: "", C: "", D: "" },
        answer: [],
        message: ""
      });
    },
    addJudgement() {
      let id = getRandomStr();
      this.judgementQuestion.push({
        id,
        title: "",
        answer: "",
        message: ""
      });
    },
    addAnswer() {
      let id = getRandomStr();
      this.answerQuestion.push({
        id,
        title: "",
        answer: "",
        message: ""
      });
    },
    async create() {
      let flag = true;
      let singleAnswer = {};
      let multipleAnswer = {};
      let judgementAnswer = {};
      let answerAnswer = {};
      // 
      this.singleQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (
          !item.options["A"] ||
          !item.options["B"] ||
          !item.options["C"] ||
          !item.options["D"]
        ) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          singleAnswer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.multipleQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (
          !item.options["A"] ||
          !item.options["B"] ||
          !item.options["C"] ||
          !item.options["D"]
        ) {
          message = "";
        } else if (item.answer.length <= 1) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          multipleAnswer[item.id] = item.answer.sort().toString();
        }
        item.message = message;
      });
      // 
      this.judgementQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          judgementAnswer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.answerQuestion.forEach(item => {
        let message = null;
        if (!item.title) {
          message = "";
        } else if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answerAnswer[item.id] = item.answer;
        }
        item.message = message;
      });

      if (flag) {
        let params = {};
        params.single = {
          question: this.singleQuestion,
          answer: singleAnswer,
          count: this.singleQuestion.length
        };
        params.multiple = {
          question: this.multipleQuestion,
          answer: multipleAnswer,
          count: this.multipleQuestion.length
        };
        params.judgement = {
          question: this.judgementQuestion,
          answer: judgementAnswer,
          count: this.judgementQuestion.length
        };
        params.answer = {
          question: this.answerQuestion,
          answer: answerAnswer,
          count: this.answerQuestion.length
        };
        // id
        params.userId = this.userInfo._id;

        params.questionType = this.questionType
        params.checkList = JSON.stringify(this.checkList)

        try {
          const result = await post("/api/questions/addItemBank", params);
          if (result.statusCode == 200) {
            this.checkList = []
            this.$message({
              type: "success",
              message: result.message,
              showClose: true
            });
            this.resetData()
          } else {
            this.$message({
              type: "warning",
              message: result.message,
              showClose: true
            });
          }
        } catch (error) {
          this.$message({
            type: "error",
            message: error.toString(),
            showClose: true
          });
        }
      } else {
        this.$message({
          type: "warning",
          message: "",
          showClose: true
        });
      }
    },
    deleteItem({ key: item, index }) {
      this[item].splice(index, 1);
    },
    openFile() {
      document.getElementById("file").click();
    },
    async upload(e) {
      try {
        let file = e.target.files[0];
        let name = file.name;
        let reg = /(\.(xls)|(xlsx))$/i;
        let titleName = name.replace(/(.*\/)*([^.]+).*/gi, "$2");
        if (reg.test(name)) {
          let formData = new FormData();
          formData.append("file", file);
          const result = await post("/api/upload/uploadFile", formData);
          if (result.statusCode == 200) {
            let data = result.data.question;
            for (let key in data) {
              data[key].forEach(item => {
                this[`${key}Question`].push(item);
              });
            }
            if (result.message) {
              this.$message({
                type: "error",
                message: result.message,
                showClose: true
              });
            } else {
              this.$message({
                type: "success",
                message: "",
                showClose: true
              });
            }
          } else {
            this.$message({
              type: "error",
              message: result.message,
              showClose: true
            });
          }
        } else {
          this.$message({
            type: "warning",
            message: ",xlsxlsx",
            showClose: true
          });
        }
      } catch (error) {
        this.$message({
          type: "error",
          message: error.toString(),
          showClose: true
        });
      }finally{
        this.$refs.pathClear.value =''
      }
    },
    resetData() {
      this.singleQuestion = [];
      this.multipleQuestion = [];
      this.judgementQuestion = [];
      this.answerQuestion = [];
    },
    async downLoadTemplate(){
      try {
        let url = '/api/downLoad/downLoadTemplate'
        window.open(url)
      } catch (error) {
        this.$message({
            showClose:true,
            type:'error',
            message:error.toString()
          })
      }
    }
  },
  computed: {
    ...mapGetters(["userInfo"]),
    isShow(){
        return this.singleQuestion.length == 0 &&
        this.multipleQuestion.length == 0 &&
        this.judgementQuestion.length == 0 &&
        this.answerQuestion.length == 0
    }
  },
}
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
</style>


